* {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif; }

#root {
  width: 100%;
  height: 100%; }

.app {
  width: 100%;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  margin: 20px auto;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  background: #ECECEC; }

.markdown, .preview {
  width: 600px;
  min-height: 400px;
  margin: 20px 0;
  background: white;
  border-radius: 5px;
  padding: 30px; }

.text {
  background: red;
  width: 100px;
  text-align: center;
  border-radius: 5px; }

.previewText {
  background: green;
  width: 100px;
  text-align: center;
  border-radius: 5px; }

.preview div {
  width: 100%;
  margin-top: 10px; }

textarea {
  width: 99%;
  height: 90%;
  border: none;
  outline: none;
  margin-top: 10px; }

.title {
  width: 100%;
  text-align: center;
  margin-top: 10px;
  font-size: 40px; }



